import { ElMessage } from 'element-plus';
export default {
  name: "LncRNAComponent",
  data() {
    return {
      defaultInput: `>DLEU2L
`,
      lncRNAinputValue: "",
      isPredicting: false,
      predictionResult: null
    };
  },
  created() {
    this.lncRNAinputValue = this.defaultInput;
  },
  methods: {
    async predict() {
      let isValid = true;
      // 拆分输入
      const sequences = this.lncRNAinputValue.split('>').filter(Boolean).map(seq => {
        const [name, ...lines] = seq.split('\n');
        let sequence = lines.join('').trim().toUpperCase();

        // 检查序列是否只包含 A, T, C, G, U
        if (!/^[ATCGU]*$/.test(sequence)) {
          ElMessage.error('The input sequence is invalid.');
          isValid = false;
          return;
        }
        // 将所有的 U 替换为 T
        sequence = sequence.replace(/U/g, 'T');
        return {
          name: name.trim(),
          sequence
        };
      });
      if (!isValid) {
        return;
      }
      // 发送请求
      const response = await fetch('https://latnet.wsleepybear.cn/lncrna-predict', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(sequences)
      });

      // 处理响应
      this.isPredicting = true;
      const result = await response.json();
      // console.log(result.result);
      this.predictionResult = result.result;
      this.isPredicting = false;
    },
    clear() {
      this.lncRNAinputValue = "";
      this.predictionResult = null;
    },
    example() {
      this.lncRNAinputValue = this.defaultInput;
    }
  }
};